import React from 'react';
import './css/slide.css';

class Slide extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            backgroundColor: props.backgroundColor,
            fontColor: props.fontColor,
            text: props.text
        };
      }

    render() {
        return(
            <div className="Slide" style={{backgroundColor: this.props.backgroundColor, color: this.props.fontColor}}>
               <p>{this.props.text}</p>
            </div>
        );
    }
}

export default Slide;